<template>
	<div id="order_list">
		<div class="list-header">
			<div :class="{active:0==tabIndex}" @click="checkTab(0)">全部</div>
			<div :class="{active:1==tabIndex}" @click="checkTab(1)">待付款</div>
			<div :class="{active:2==tabIndex}" @click="checkTab(2)">待发货</div>
			<div :class="{active:3==tabIndex}" @click="checkTab(3)">待收货</div>
			<div :class="{active:4==tabIndex}" @click="checkTab(4)">待评价</div>
		</div>
		<div class="list-container">
			
			<!-- 列表项 -->
			<template v-if="orderList.length > 0">
			<div class="list-wrap" v-for="(item,index) in orderList">
				<div class="shop-info">
					<span class="shop-label">{{item.directSale==1?'自营':'加载'}}</span>
					<span>{{item.orgName}}</span>
					<span class="fl-right negative" v-if="item.status==0">已取消</span>
					<span class="fl-right active" v-else-if="item.status==2">已付款</span>
					<span class="fl-right normal" v-if="item.status!=0&&item.status!=2">{{status[item.status]}}</span>
				</div>

				<div class="list-item" v-for="(good,index) in item.goodsList" @click="toOrderDetail(item.subOrderId)">
					<div class="good-img">
						<img class="goods-img" :src="good.imagePath" alt=""/>
					</div>
					<!-- 商品信息 start -->
					<div class="content">
							<div class="goods-name">{{good.name}}</div>
							<div class="goods-norms">{{good.productSize}} <span v-if="good.packing.name != ''">({{good.packing.name}})</span></div>
							<div class="goods-prices">
	              <div class="packprice">包装费：￥{{good.packPrice}}</div>
								<span class="price"><em class="price-icon">￥</em>{{good.discountPrice}}</span>
								<span class="num fl-right"><em>x</em>{{good.buyNum}}</span>
							</div>
					</div>
				</div>
				<div class="list-price">
					<span>共{{item.total}}件商品</span><span class="price">合计:<em class="price-icon">￥</em>{{item.realAmount}}</span>
				</div>
				<div class="list-btns">
					<template v-if="item.status==0">
						<span @click="showDel(item.subOrderId)">删除订单</span>
					</template>
					
					<template v-if="item.status==1">
						<span class="active" @click="toPayment(item.id,item.realAmount)">立即支付</span>
					</template>
					
					<!--已支付-->
					<template v-if="item.status==2 || item.status==3">
						<span @click="contactService(item)">联系客服</span>
					</template>
					
					<!--已发货-->
					<template v-if="item.status==5 || item.status==6">
						<span @click="toOrderDetail(item.subOrderId)">查看物流</span>
						<span class="active" @click="confirm(item.subOrderId)">确认收货</span>
					</template>
					
					<template v-if="item.status==7">
						<span class="active" @click="toSelectGoods(item.subOrderId)">立即评价</span>
					</template>
					
					<template v-if="item.status==8">
						<span>申请售后</span>
						<span @click="showDel(item.subOrderId)">删除订单</span>
					</template>
					
					<!--<span v-if="item.status==2 || item.status==3 || item.status==4 || item.status==5 || item.status==6" @click="toAfterSale">申请售后</span>
					<span v-if="item.status==0 || item.status==8 || item.status==14" @click="showDel">删除订单</span>
					<span v-if="tabIndex==2 || item.status==3">联系客服</span>
					<span v-if="tabIndex==4 || item.status==7" class="active" @click="toSelectGoods">立即评价</span>
					<span v-if="tabIndex==3 || item.status==5 || item.status==6" @click="toKuaidi">查看物流</span>
					<span v-if="tabIndex==3 || item.status==5" class="active" @click="confirm">确认收货</span>-->
				</div>
			</div>
			</template>
			<template v-else>
				<div style="text-align: center;">暂无数据</div>
			</template>
		</div>
	</div>
</template>

<script>
//import Popup from '../Popup/Popup'
export default {
	data(){
		return {
			tabIndex:0,
			status:['已取消','待支付','已支付','待发货','发货中','已发货','待收货','待评价','已完成','','争议处理中','申请退货','退货中','退货已收货','退货完成'],
			orderList:[],
		}
	},
  components: {
//	Popup
  },
  created(){
  	this.$Api.untilSessionId().then(()=>{
  		this.getOrderList(99);
  	})
  },
  methods:{
  	
  	checkTab(index){
  		this.tabIndex = index;
  		let status 
  		switch (index){
  			case 0:
  				status = 99
  				break;
  			case 1:
  				status = 1
  				break;
  			case 2:
  				status = 3
  				break;
  			case 3:
  				status = 6
  				break;
  			case 4:
  				status = 7
  				break;
  			default:
  				break;
  		}
    	this.getOrderList(status)
  	},
  	toAfterSale(){
    	this.$router.push({name:'AfterSale'});
  	},
  	logisticsInfo(){
			let url = this.logistics.detailUrl + this.logistics.sn
  		window.location.href = url
  	},
  	//删除订单
  	showDel(subOrderId){
      this.$popup({
        content:'确认要删除选中的订单?'
      }).then((res) => {
        // console.log(res)
        if(res == 'sure'){
        	this.getOrderUpdate(2,subOrderId)
        }
      })
    },
    //确认收货
    confirm(subOrderId){
      this.$popup({
        content:'确认已经收到货物?'
      }).then((res) => {
        // console.log(res)
        if(res == 'sure'){
        	this.getOrderUpdate(4,subOrderId)
        }
      })
    },
    //查看快递
//  toKuaidi(){
//  	window.location = "https://m.kuaidi100.com/result.jsp?nu=123123"
//  },
    toOrderDetail(subOrderId){
    	this.$router.push({name:'OrderDetail',params:{id:subOrderId}})
    },
    toPayment(orderId,realAmount){
    	if(this.$call.testType().wechat){
    		this.$router.push({name:'Cashier',query:{orderId:orderId,realAmount:realAmount}})
			}else{
				let params = {
					action:0,
					amount:realAmount,
					orderId:orderId
				}
				checkOutCounter(params)
			}
    },
    toSelectGoods(subOrderId){
//  	this.$router.push({name:'SelectGoods',params:{id:subOrderId}})
    },
    getOrderList(status){
    	this.$Api.getOrderList(1,status).then((res)=>{
    		if(res.q.s == 0){
    			this.orderList = res.q.orders
    		}
    	})
    },
    getOrderUpdate(a,id){
    	this.$Api.getOrderUpdate(a,id).then((res)=>{
    		// console.log(res)
    		if(res.q.s == 0){
					this.tabIndex = 0
    			this.getOrderList(99)
    			if(a != 3){
    				this.$toast('操作成功')
    				setTimeout(()=>{
    					this.$router.replace({name:'OrderList'})
    				},1000)
    			}else if(a == 3){
    				let orderId = res.q.orderId
    				if(this.$call.testType().wechat){
				      this.$router.push({name: 'Cashier',query:{orderId:orderId,realAmount:this.Order.goodsAmount}})
    				}else{
    					let params = {
      					action:0,
      					amount:this.Order.goodsAmount,
      					orderId : orderId
      				}
    					checkOutCounter(params)
    				}
    			}
    		}
    	})
    },
    contactService(item){
    	if(this.$call.testType().wechat){
    		// console.log(item)
			}else{
				contactCustomerService(item)
			}
    }
    
  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#order_list{
		.list-header{
			position: fixed;
			top: 1.17rem;
			width: 100%;
			display: flex;
			background: #fff;
			z-index: 100;
			>div{
				position: relative;
				font-size: 0.4rem;
				height: 1.17rem;
				line-height: 1.17rem;
				flex: 1;
				text-align: center;
				border-bottom: 1px solid #ccc;
				/*&:after{
					@include bottom;
				};*/

			}
			.active{
				color: $label-color;
				border-bottom: 2px solid $label-color;
			}
		}
		.list-container{
			position: absolute;
			top: 1.17+1.17rem;
			width: 100%;
			background: #f0f0f0;
			.list-wrap{
				background: #fff;
				margin-bottom: 0.27rem;
			}
			.shop-info{
				position: relative;
				width: 100%;
				height: 1.17rem;
				line-height: 1.17rem;
				padding: 0 0.4rem;
				font-size: 0.37rem;
				color: #333;
				&:after{
					@include bottom;
				};
				.shop-label{
					color: $label-color;
				}
			}
			.active{
				color: #fe4436;
			}
			.negative{
				color: #999;
			}
			.normal{

			}
			.list-item{
				position: relative;
				display: flex;
				padding: 0.21rem 0.32rem;
    		/*border-bottom: 1px solid #cccccc;*/
				&:after{
					@include bottom;
				};
				.good-img{
					width: 2.13rem;
					height: 2.13rem;
					.goods-img{
						width: 2.13rem;
						height: 2.13rem;
						border: 1px solid #dedede;
						border-radius: 0.11rem;
					}
				}
				.content {
					flex: 1;
					margin-left: 0.35rem;
					display: flex;
					justify-content: space-around;
					flex-direction: column;
					.goods-name {
						font-size: 0.37rem;
						color: $text-color;
						@include textflow-two;
					}
					.goods-norms {
						color: $text-color2;
						font-size: 0.32rem;
						@include textflow-one;
					}
					.goods-prices {
						.packprice{
							color: $price-color;
							font-size: 0.32rem;
						}
						.price {
							color: $price-color;
							font-size: 0.43rem;
							.price-icon {
								font-size: 0.32rem;
							}
						}
						.num {
							color: $text-color2;
							font-size: 0.32rem;
							>em {
								font-size: 0.24rem;
							}
						}
					}
				}
			}
			.list-price{
				position: relative;
				font-size: 0.4rem;
				height: 1.17rem;
				line-height: 1.17rem;
				padding: 0 0.4rem;
				text-align: right;
				color: #333;
				&:after{
					@include bottom;
				};
				.price{
					font-size: 0.43rem;
					margin-left: 0.25rem;
				}
				.price-icon{
					font-size: 0.37rem;
				}
			}
			.list-btns{
				/*font-size: 0;*/
				width: 100%;
				height: 1.17rem;
				line-height: 1.17rem;
				text-align: right;
				padding: 0 0.4rem;
				>span{
					display: inline-block;
					font-size: 0.37rem;
					text-align: center;
					height: 0.75rem;
					line-height: 0.72rem;
					margin-left: 0.27rem;
					border: 1px solid #ccc;
					border-radius: 0.37rem;
					width: 2.0rem;
					color: #333;
				}
				.active{
					border: 1px solid $label-color;
					color: $label-color;
				}
			}

		}
	}
</style>


